// ** React Imports
import { Link } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
// ** Custom Components
import Avatar from '@components/avatar'

// ** Store & Actions
import { store } from '@store/store'
import { getTask, deleteTask } from '../store'

// ** Icons Imports
import { MoreVertical, Trash2, Eye, Edit, CheckCircle, XCircle } from 'react-feather'

// ** Reactstrap Imports
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Col, Badge } from 'reactstrap'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.clientname.charAt(0) || 'T'}
    />
  )

}

const statusObj = [
  '',
  'light-primary',
  'light-success',
  'light-warning'
]


const priorityOptions = ['', 'Low', 'Medium', 'High']

export const columns = [
  {
    name: 'Client',
    sortable: true,
    minWidth: '172px',
    sortField: 'clientname',
    selector: row => row.clientname,
    cell: row => {
      return (
        <Link
          to={`/recurring-task/view/${row.id}`}
          className='user_name text-truncate text-body'
          onClick={() => store.dispatch(getTask(row.id))}>
          {renderClient(row)}
          <span className='text-capitalize'>{row.clientname}</span></Link>)
    }
  },
  {
    name: 'Service',
    minWidth: '172px',
    sortable: true,
    sortField: 'servicename',
    selector: row => row.servicename,
    cell: row => <span className='text-capitalize'>{row.servicename}</span>
  },
  {
    name: 'Priority',
    minWidth: '230px',
    sortable: true,
    sortField: 'priority',
    selector: row => row.priority,
    cell: row => <Badge color={statusObj[row.priority]}>{priorityOptions[row.priority]}</Badge>
  },
  {
    name: 'Assignee',
    minWidth: '138px',
    sortable: false,
    cell: row => (
      row.assignee.map((obj, key) => { return (<Badge color='light-primary' key={key}>{obj.name}</Badge>) })
    )
  },
  {
    name: 'Reviewer',
    minWidth: '138px',
    sortable: false,
    cell: row => (
      row.reviewer.map((obj, key) => { return (<Badge color='light-primary' key={key}>{obj.name}</Badge>) })
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => (
      <div className='column-action d-flex align-items-center'>
        <Can I={'read'} a={'recurring-task'}>
          <Col tag={Link} lg={4}
            to={`/recurring-task/view/${row.id}`}
            onClick={() => store.dispatch(getTask(row.id))} >
            <Eye className='cursor-pointer mt-0' size={16} />
          </Col>
        </Can>
        <Can I={'write'} a={'recurring-task'}>
          <Col tag={Link} to={`/recurring-task/edit/${row.id}`} lg={4}
            onClick={() => store.dispatch(getTask(row.id))} >
            <Edit
              className='cursor-pointer ms-1 mt-0' size={16} />
          </Col>
        </Can>
        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>
          <DropdownMenu>
            <Can I={'delete'} a={'recurring-task'}>
              <DropdownItem
                tag='a'
                href='/'
                className='w-100'
                onClick={e => {
                  e.preventDefault()
                  store.dispatch(deleteTask(row.id))
                }}
              >
                <Trash2 size={14} className='me-50' />
                <span className='align-middle'>Delete</span>
              </DropdownItem>
            </Can>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
    )
  }
]
